export default {
	data() {
		return {
			type: 1,
			loadMoreText: '加载中...',
			showLoadMore: false,
			isMore: true,
			PageNo: 1,
			PageSize: 10,
			mainItem: [],
			keyWords: '' ,//搜索关键词
			countDownObject : {
				countDownStr : '',
				count_down : 0
			},
			timer : null
		};
	},
	onShow() {
		this.$store.commit('judgeLogin'); //判断登录状态
		this.getStartTime();
	},
	onHide(){
		console.log('页面隐藏')
		if(this.timer){
			clearInterval(this.timer);
		}
	},
	onLoad(option) {
		if (option.type) this.type = option.type;
		this.initData();
		// this.getStartTime();
	},
	onUnload() {
		console.log('页面卸载');
		if(this.timer){
			clearInterval(this.timer);
		}

		this.PageNo = 1;
		this.mainItem = [];
		this.loadMoreText = '加载更多';
		this.showLoadMore = false;
	},
	onBackPress(e){
	  // console.log("监听返回按钮事件",e);
	  // uni.navigateTo({
	  //   url:"/pages/details/details?type=2"
	  // })
	  // // 此处一定姚要return为true，否则页面不会返回到指定路径
	  // return true;
	},
	onReachBottom() {
		//下拉加载
		this.showLoadMore = true;
		setTimeout(() => {
			this.setListData();
		}, 300);
	},
	onPullDownRefresh() {
		//上拉刷新
		this.initData();
	},
	methods: {
		countdown() {
			let that = this;
			that.timer = setInterval(function() {
				if (that.countDownObject.count_down > 0) {
					that.countDownObject.count_down -= 1;
					let t = that.countDownObject.count_down;
					let day = Math.floor(t / 86400);
					let hour = Math.floor((t / 3600) % 24);
					let min = Math.floor((t / 60) % 60);
					let sec = Math.floor((t) % 60);
					day = day < 10 ? "0" + day : day;
					hour = hour < 10 ? "0" + hour : hour;
					min = min < 10 ? "0" + min : min;
					sec = sec < 10 ? "0" + sec : sec;
					let format = "";
					if(day == '00'){
						format = `待开始：${hour}时${min}分${sec}秒`;
					}else{
						format = `待开始：${day}天${hour}时${min}分${sec}秒`;
					}
					that.$set(that.countDownObject,'countDownStr',format)
				} else {
					// 进行判断 如果数据内所有的倒计时已经结束，那么结束定时器， 如果没有那么继续执行定时器
					clearInterval(that.timer);
					if (that.countDownObject.count_down == -1) {
						that.$set(that.countDownObject,'countDownStr','已开始')
					} else if (that.countDownObject.count_down == -2) {
						that.$set(that.countDownObject,'countDownStr','已结束')
					}
				}
				// that.$set(that.countDownObject,'countDownStr',that.countDownObject.countDownStr)
				console.log(that.countDownObject)
			}, 1000);
		},
		getStartTime(){
			let _this = this;
			_this.$request.product
				.getTime({
					class_id: _this.type,
				}).then((res)=>{
					console.log(res.data)
					res.data.countDownStr = '';
					_this.countDownObject = res.data;
					// _this.countDownObject.countDownStr = '';
					// this.startTime = res.data.count_down;
					_this.countdown();
				})
		},
		search(res) {
			this.keyWords = res.value;
			this.initData();
		},
		dumpInfo(id) {
			uni.navigateTo({
				url: '/pages/product/info?id=' + id
			});
		},
		initData() {
			//刷新数据
			setTimeout(() => {
				this.PageNo = 1;
				this.mainItem = [];

				this.getPage();
				uni.stopPullDownRefresh();
			}, 300);
		},
		setListData() {
			//加载更多数据
			this.PageNo += 1;
			if (this.isMore) this.getPage();
		},
		getPage() {
			//加载数据
			var _this = this;
			uni.showLoading({
				title: '数据查询中'
			});
			_this.$request.product
				.getGoodsList({
					page: _this.PageNo,
					class_id: _this.type,
					keyWords: _this.keyWords
				})
				.then(data => {
					uni.hideLoading();
					if (data.status == 1) {
						_this.mainItem = _this.mainItem.concat(data.data);
						if (data.data.length < _this.PageSize) {
							this.loadMoreText = '没有更多数据了';
							_this.isMore = false;
						} else {
							_this.showLoadMore = false;
							_this.isMore = true;
						}
					} else {
						_this.showLoadMore = false;
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				})
				.catch(err => {
					_this.showLoadMore = false;
					uni.hideLoading();
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		}
	}
};
